@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

* {
  font-family: 'Montserrat', sans-serif;
}


.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;

}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;

}
.info input {
  font-size: 1.2em;
  padding: .4em;
  border: none;
  border-radius: 10px;
  background-color: rgb(0, 102, 161);
  color: white;
}
label {
  padding: 1em 0;
  font-size: 1.1em;
  font-weight: 600;
  color: white;
}
#review {
  width: 30%;
  
}
button {
  margin-top: 3em;
  font-size: 1.2em;
  padding: 1em;
  border-radius: 10px;
  background-color: rgb(0, 102, 161);
  color: white;
  border:none;
}
button:hover {
  cursor: pointer;
  color: rgb(0, 102, 161);
}
.btn {
  width: 200px;
  padding: 1em;
  display: flex;
  align-self: center;
  margin-bottom: 10px;
}
.revMap {
  background-color: rgb(0, 102, 161);
  display: flex;
  align-self: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 10px;
  padding: 1em;
  width: 800px;
  color: white;
}